
import { defineComponent } from 'vue';

export default defineComponent({
	props: {
		allLists: {
			type: Array as () => QAList[],
			default: () => [],
		},
		modelValue: {
			type: String,
			default: '',
		},
	},

	data(): {
		selectList: string,
	} {
		return {
			selectList: this.modelValue,
		};
	},

	watch: {
		modelValue() {
			this.selectList = this.modelValue;
		},
	},

	methods: {
		handleChange() {
			this.$emit('update:model-value', this.selectList);
		},
	},
});
